.header {
    display: flex;
    flex-direction: row;
}

.header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;

    h1 {
        font-family: var(--font-family);
        font-weight: 800;
        font-size: 62px;
        line-height: 75px;
        letter-spacing: -0.04em;
    }

    p {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        color: var(--color-text);

        margin-top: 1.5rem;
    }
}

/* input */
.header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;

    input {
        flex: 2;
        width: 100%;
        min-height: 50px;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        background: var(--color-footer);
        border: 2px solid var(--color-footer);
        padding: 0 1rem;
        outline: none;
        color: #fff;

        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }

    button {
        flex: 0.9;
        width: 100%;
        min-height: 50px;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        background: #ff4820;
        border: 2px solid #ff4820;
        padding: 0 0.5rem;
        cursor: pointer;
        color: #fff;
        outline: none;

        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }
}

/* People */
.header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;

    margin-top: 2rem;

    img {
        width: 181.79px;
        height: 38px;
    }

    p {
        margin: 0 0 0 1rem;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 28px;
        color: #fff;
        text-align: center;
    }
}

/* Image */
.header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: baseline;

    img {
        width: 100%;
        height: 100%;
    }
}

/* responsive */
@media screen and (max-width: 1050px) {
    .header {
        flex-direction: column;
    }

    .header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .header-content {
        h1 {
            font-size: 48px;
            line-height: 60px;
        }

        p {
            font-size: 16px;
            line-height: 24px;
        }
    }

    .header-content__people {
        flex-direction: column;

        p {
            margin: 0;
        }

        img {
            margin-bottom: 5px;
        }
    }

    .header-content__input {
        input,
        button {
            font-size: 16px;
            line-height: 24px;
        }
    }
}

@media screen and (max-width: 490px) {
    .header-content {
        h1 {
            font-size: 36px;
            line-height: 48px;
        }

        p {
            font-size: 14px;
            line-height: 14px;
        }
    }

    .header-content__input {
        input,
        button {
            font-size: 12px;
            line-height: 16px;
        }
    }
}
